import { Component, OnInit, Input } from "@angular/core";
import { ArticlesService } from "../articles.service";
import { CategoriesService } from "../../categories/categories.service";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { AuthenticationService } from "src/app/core/services";


@Component({
    templateUrl: './article.edit.template.html',
    styleUrls: ['./article.edit.styles.scss']
})
export class ArticlesEditComponent {


	public articleForm: FormGroup;
	public submitted: boolean;
	loaded:boolean;


    articleId;
    articleData;
    categoryids = [];
    categories = [];
    subcategories = [];

    constructor(
		private authentication: AuthenticationService,
		private categoryService: CategoriesService,
		private articleService: ArticlesService,
		private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private toast: ToastrService,
	 	private router: Router,
    ){}

    async ngOnInit(){
        await this.route.paramMap.subscribe(params => { this.articleId = params.get("pageId") });

        await this.articleService.getArticle(this.articleId).then(data => {
            this.articleData = data;
        })

        await this.categoryService.getCategories().then((data) => {
            this.categoryids = data;
        });

        for (let index = 0; index < this.categoryids.length; index++) {
            const element = this.categoryids[index];
            this.categoryService.getCategory(element).then((data) => {
                this.categories.push(data);
                if(index + 1 == this.categoryids.length){
                    this.loaded = true;
				}
            });
		}

		this.loaded = false;
		this.submitted = false;
	 	this.articleForm = this.formBuilder.group({
	 		title: [this.articleData['name'], [Validators.required]],
			desc: [this.articleData['description'], [Validators.required]],
			category: [this.articleData.categories[0],[Validators.required]],
			subcategory: [''],
			image_url: [''],
			content:[this.articleData.contents]
	 	});

        this.loadSubcategories();
	}

	loadSubcategories(){
		this.subcategories = [];
		this.categories.forEach(element => {
			if(element.info.id == this.articleForm.get('category').value){
				if(element.sub.length < 1) return;
				this.categoryService.getCategory(element.sub).then(data => {
					this.subcategories.push(data);
				})
			}
		});
	}


	create() {
		console.log(this.articleForm);
		this.submitted = true;
		if (!this.articleForm.valid) {
			//eerorservice
			this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
			return;
		}

		var categories = [];
		categories.push(this.articleForm.get('category').value);
		if(this.articleForm.get('subcategory').value != "" ){
			categories.push(this.articleForm.get('subcategory').value);
		}
		console.log(categories);

		let data = {
			"author": this.authentication._credentials['id'],
			"contents": this.articleForm.get('content').value,
			"datum": new Date(),
			"name": this.articleForm.get('title').value,
			"location": "",
			"perms": [""],
			"tags": [""],
			"image_url": this.articleForm.get('image_url').value,
			"description": this.articleForm.get('desc').value,
			"categories": categories
		};

		console.log(data);

		this.articleService.editArticle(data,this.articleId).then((data) => {
				//spis toast tady
				this.router.navigate(['/redactor/articles'])
			}, (error) => {
				this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
				//todo errorhandler
				this.submitted = false;
				console.log("error:" + error);
			}
		);
	}


}
