import { Routes } from "@angular/router";
import { ArticlesComponent } from "./articles.component";
import { ArticlesCreateComponent } from "./create/article.create.component";
import { ArticlesEditComponent } from "./edit/article.edit.component";

/**
 *  
 * 
 * 
 */


// Actual moudule routes
export const articles_routes: Routes = [ 
    {
        path: 'articles',
        loadChildren: 'src/app/common/modules/articles/articles.module#ArticlesModule'
    }
] 

// Required for lazy-loading
export const routes: Routes = [ 
    {
        path: '',
        component: ArticlesComponent
    },
    {
        path: 'create',
        component: ArticlesCreateComponent
    },
    {
        path: 'edit/:pageId',
        component: ArticlesEditComponent
    },
]
